.countdown {
  position: absolute;
  max-width: 165px;
  left: 35px;
  top: 10px;
  @include sofia-medium;
  h3 {
    text-align: center;
    font-size: 14px;
    width: 100%;
    color: $color-pink;
    @include sofia-medium;
  }
  &-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  &-time {
    color: $color-pink;
    font-size: 22px;
    @include sofia-black;
  }
  &-text {
    color: $color-pink;
    font-size: 22px;
    @include sofia-black;
  }
  &-number {
    margin-right: 5px;
  }
}
