.footer {
  background-color: white;
  padding: 73px 0;
  @include flex-center;
  @include media-breakpoint-down(sm) {
    background-color: transparent;
  }
  h3 {
    text-align: center;
    color: #444;
    font-size: 18px;
    line-height: 50.97px;
    margin-bottom: 17px;
    @include lato-bold;
  }
  p {
    color: #8892a0;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    @include lato-regular;
    &:nth-of-type(2) {
      padding: 25px 0;
    }
  }
  &__container {
    max-width: 1060px;
    padding: 0 15px;
  }
}
