.login-background {
  background-color: white;
  background-size: cover;
  background-position: center -90px;
  img {
    width: 100%;
    max-width: 350px;
    padding: 45px 0 36px 0;
    margin: 0 auto;
    display: block;
  }
}
.login-box {
  background-color: white;
  margin: 0 auto;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.09);
  border-radius: 4px;
  padding: 58px 32px 45px 32px;
  width: 100%;
  max-width: 350px;
  h2 {
    color: #444;
    font-size: 35px;
    margin-bottom: 44px;
    @include lato-bold;
  }
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    input {
      width: 100%;
      height: 30px;
      padding: 11px;
      background-color: white;
      border-radius: 4px;
      border: 1px solid #ced4da;
      &:first-of-type {
        margin-bottom: 17px;
      }
      &::placeholder {
        color: #868e96;
        font-size: 13px;
        @include lato-regular;
      }
    }
    button {
      background-color: white;
      color: #528bff;
      font-size: 16px;
      letter-spacing: 0.4px;
      width: 105px;
      height: 40px;
      border: 1px solid #528bff;
      border-radius: 2px;
      @include lato-regular;
    }
  }
  &__recover {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 9px;
    margin-bottom: 39px;
    width: 100%;
    a {
      color: #469ad4;
      font-size: 13px;
      @include lato-regular;
      &:hover {
        color: #007bff;
      }
    }
  }
}
