.essentials-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.content-box {
  display: flex;
  flex-wrap: wrap;
}
.employee {
  max-width: 790px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  margin-bottom: 21px;
  margin-top: 30px;
  @include media-breakpoint-down(md) {
    justify-content: center;
    align-items: center;
  }
  .three-column__right {
    h2 {
      margin-bottom: 40px;
    }
  }
  &__left {
    margin-right: auto;
    padding-right: 10px;
    padding-top: 50px;
    @include media-breakpoint-down(md) {
      max-width: 583px;
      margin: 0 auto;
      width: 100%;
      margin-bottom: 35px;
    }
    a {
      display: block;
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.49px;
      margin-bottom: 23px;
      @include sofia-medium;
      &:nth-of-type(2) {
        margin-bottom: 78px;
        @include media-breakpoint-down(md) {
          margin-bottom: 23px;
        }
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  &__inner {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    width: 100%;
    flex-wrap: wrap;
    @media (max-width: 991px) {
      max-width: 365px;
      justify-content: space-between;
    }
    @media (max-width: 575px) {
      justify-content: center;
    }
  }
  &__item {
    max-width: 172px;
    text-align: center;
    margin-left: 19px;
    &:first-of-type {
      margin-left: 0;
    }
    @include media-breakpoint-down(md) {
      margin-left: 0;
      margin-bottom: 19px;
    }
    img {
      width: 100%;
    }
    h4 {
      color: #444;
      font-size: 12px;
      margin-bottom: 12px;
      margin-top: 23px;
      @include raleway-extrabold;
    }
    p {
      color: #8892a0;
      font-size: 11px;
      margin-bottom: 18px;
      @include raleway-regular;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}
.webvention-essentials {
  h1 {
    @include media-breakpoint-down(lg) {
      margin-left: 0;
    }
  }
}
