body {
  @include lato-regular;
}
.three-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 132px 0 0 0;
  width: 100%;
  &__right {
    white-space: nowrap;
    text-align: right;
  }
  @include media-breakpoint-down(md) {
    flex-direction: column;
    align-items: center;
    padding: 55px 15px 0 15px;
  }
  &__left {
    max-width: 323px;
    width: 100%;
    @include media-breakpoint-down(md) {
      margin-bottom: 20px;
    }
    img {
      width: 100%;
    }
    h2 {
      margin-top: 18px;
      color: $color-pink;
      font-size: 38px;
      @include sofia-black;
      position: relative;
      &:before {
        content: "";
        width: 180px;
        height: 2px;
        background-color: #383838;
        position: absolute;
        top: -6px;
      }
    }
  }
  &__center {
    width: 100%;
    padding-left: 145px;
    @include media-breakpoint-down(md) {
      padding-left: 0;
      margin-bottom: 20px;
    }
    h1 {
      color: $color-pink;
      font-size: 45px;
      margin-top: 45px;
      text-align: left;
      line-height: 30px;
      @include romantically;
      @include media-breakpoint-down(lg) {
        font-size: 32px;
      }
      @include media-breakpoint-down(md) {
        text-align: center;
        line-height: 105px;
      }
    }
  }
  &__right {
    h2 {
      margin-top: 68px;
      color: $color-pink;
      font-size: 33px;
      @include sofia-black;
      @media (max-width: 991px) {
        text-align: center;
      }
    }
  }
}
.btn-blue {
  display: flex;
  justify-content: flex-end;
  @include media-breakpoint-down(sm) {
    display: none;
  }
  &__button {
    color: white;
    background-color: $color-dark-blue;
    width: 163px;
    height: 63px;
    font-size: 25px;
    transition: 0.3s;
    @include raleway-regular;
    @include flex-center;
    &:hover {
      text-decoration: none;
      color: white;
      background-color: $color-dark-blue;
    }
  }
}
.container {
  max-width: 1260px;
  padding: 0 15px;
  margin: 0 auto;
}
.standby {
  position: relative;
  &:before {
    content: "";
    background-image: url("images/standby.svg");
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 120px;
    background-size: contain;
    background-repeat: no-repeat;
    @include sofia-black;
  }
}
input {
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
textarea {
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
@media (max-width: 991.98px) {
  .employee__left {
    position: absolute;
    top: -185px;
    text-align: center;
    max-width: 100%;
    width: 100%;
  }
  .essentials-background .content-box {
    position: relative;
  }
  .three-column__right h2 {
    margin-top: 125px;
    text-align: center;
  }
}
