.party-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.pack-text {
  margin-top: 46px;
  margin-bottom: 48px;
  max-width: 750px;
  margin-left: auto;
  @include media-breakpoint-down(md) {
    margin-right: auto;
  }
  h3 {
    color: #444;
    font-size: 19px;
    margin-bottom: 21px;
    @include raleway-extrabold;
  }
  p {
    color: #8892a0;
    font-size: 11px;
    line-height: 16.07px;
    margin-bottom: 65px;
    @include raleway-extrabold;
  }
  a {
    color: #51a0d5;
    font-size: 19px;
    letter-spacing: 0.49px;
    @include sofia-medium;
  }
}
