.video-popup {
  position: fixed;
  background-color: rgb(0, 0, 0, 0.5);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  margin-top: -150px;
  visibility: hidden;
  transition: 0.3s;
  @include flex-center;
  iframe {
    max-width: 100%;
  }
  &__container {
    position: relative;
    outline: none;
  }
  &__close {
    position: absolute;
    top: 40px;
    right: 50px;
    cursor: pointer;
    width: 30px;
    height: 30px;
  }
  &__active {
    opacity: 1;
    visibility: visible;
    margin-top: 0;
  }
}
