.close-hero {
  background-color: white;
  background-size: cover;
  background-position: center -90px;
  img {
    display: block;
    max-width: 625px;
    width: 100%;
    padding: 225px 0 30px 0;
    margin: 0 auto;
    @media (max-width: 640px) {
      padding: 100px 0 30px 0;
    }
    @media (max-width: 1024px) {
      padding: 185px 0 30px 0;
    }
    @media (max-width: 767px) {
      padding: 135px 0 30px 0;
    }
  }
  &__content {
    @media (max-width: 767px) {
      padding-bottom: 35px;
    }
    a {
      margin-top: 5px;
      display: block;
      text-align: center;
      color: #528bff;
      font-size: 16px;
      @include sofia-black;
    }
    strong {
      display: block;
      text-align: center;
      color: $color-pink;
      font-size: 19px;
      margin-bottom: 30px;
      @include sofia-black;
    }
    p {
      display: block;
      text-align: center;
      color: #528bff;
      font-size: 16px;
      @include sofia-medium;
    }
  }
}
