.expert-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
  h1 {
    @include media-breakpoint-down(xs) {
      line-height: 130px;
    }
  }
}
.ex-panels {
  &__inner {
    margin-top: 38px;
    margin-bottom: 85px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 485px;
    margin-left: 475px;
    flex-wrap: wrap;
    @include media-breakpoint-down(lg) {
      margin-left: auto;
      margin-right: auto;
    }
    @include media-breakpoint-down(md) {
      margin-top: 60px;
    }
    @include media-breakpoint-down(xs) {
      max-width: 205px;
    }
  }
  .side {
    @include media-breakpoint-down(xs) {
      margin-bottom: 20px;
    }
  }
}
