.why-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.why-text {
  margin-left: auto;
  max-width: 750px;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-top: 75px;
  @include media-breakpoint-down(md) {
    max-width: 575px;
    margin-top: 55px;
    margin-right: auto;
  }
  a {
    display: block;
    min-width: 285px;
    color: $color-dark-blue;
    font-size: 19px;
    letter-spacing: 0.49px;
    margin-bottom: 33px;
    @include sofia-medium;
  }
}
