.video-box {
  background-color: white;
  background-size: cover;
  background-position: center -80px;
  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 1210px;
    padding: 57px 15px 20px 15px;
    margin: 0 auto;
    @include media-breakpoint-down(lg) {
      flex-direction: column;
    }
  }
  &__title {
    @include media-breakpoint-down(md) {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }
  }
  &__left {
    width: 100%;
    max-width: 460px;
    margin-right: 25px;
    @include media-breakpoint-down(lg) {
      margin: 0 auto;
    }
    img {
      width: 100%;
      max-width: 323px;
    }
    h2 {
      color: $color-pink;
      font-size: 33px;
      letter-spacing: 0.8px;
      margin-bottom: 30px;
      @include sofia-black;
    }
  }
  &__side {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include media-breakpoint-down(xs) {
      flex-direction: column;
    }
    .side {
      @include media-breakpoint-down(xs) {
        margin: 0 auto;
      }
    }
  }
  &__right {
    h1 {
      color: $color-dark-blue;
      font-size: 18px;
      letter-spacing: 1.92px;
      margin-bottom: 20px;
      @include raleway-semibbold;
      @include media-breakpoint-down(lg) {
        text-align: center;
        margin-top: 35px;
      }
    }
    video {
      display: block;
      max-width: 100%;
      min-width: 100%;
      @include media-breakpoint-down(lg) {
        margin: 0 auto;
        max-width: 465px;
        width: 100%;
      }
    }
  }
}
