.header {
  background-color: $color-blue;
  outline: none;
  @include media-breakpoint-down(lg) {
    height: 60px;
  }
  &__container {
    height: 65px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    z-index: 10;
    @include media-breakpoint-down(lg) {
      height: 100%;
      position: fixed;
      right: 0;
      z-index: 100;
      align-items: flex-start;
      background-color: $color-blue;
      transform: translateX(100%);
      transition: 0.3s;
    }
  }
  &__mobile-open {
    transform: translateX(0);
  }
  .mobile-background {
    visibility: hidden;
    opacity: 0;
    position: fixed;
    background-color: rgb(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    transition: 0.3s;
    &__active {
      visibility: visible;
      opacity: 1;
    }
  }
  &__active {
    font-weight: 800 !important;
  }
  ul {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      align-items: flex-end;
      padding-top: 40px;
      min-width: 200px;
    }
    ul {
      li {
        a {
          font-weight: 400;
          font-size: 11px;
        }
      }
    }
  }
  li {
    transition: 0.3s;
    position: relative;
    @include media-breakpoint-down(lg) {
      width: 100%;
    }
    img {
      position: absolute;
      width: 20px;
      top: 14px;
      left: 5px;
      display: none;
      @include media-breakpoint-down(lg) {
        display: inline-block;
      }
    }
    ul {
      visibility: hidden;
      opacity: 0;
      transform: translateY(15px);
      background-color: $color-dark-blue;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;
      transition: 0.4s;
      position: absolute;
      top: 65px;
      width: 100%;
      padding-top: 30px;
      padding-bottom: 10px;
      z-index: 500;
      @include media-breakpoint-down(lg) {
        position: relative;
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
        top: 0;
        padding-top: 0;
        padding-bottom: 0;
        background-color: $color-blue;
        display: none;
        transition: 4.4s;
      }
      a {
        height: 30px;
        text-transform: capitalize;
        color: white;
        @include media-breakpoint-down(lg) {
          color: #383838;
        }
        &:hover {
          color: $color-blue;
        }
      }
    }
    &:hover {
      background-color: $color-dark-blue;
      ul {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
      }
      img {
        filter: brightness(100);
      }
    }
  }
  a {
    color: #383838;
    font-size: 14px;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    width: 100%;
    height: 65px;
    padding: 0 10px;
    transition: 0.3s;
    @include flex-center;
    @include raleway-semibbold;
    @media (max-width: 1235px) {
      padding: 0 8px;
      font-size: 12px;
    }
    @include media-breakpoint-down(lg) {
      font-size: 14px;
      width: 100%;
      height: 50px;
      padding: 0 10px 0 30px;
      justify-content: flex-end;
    }
    &:hover {
      color: white;
      text-decoration: none;
    }
  }
  #close-menu {
    display: none;
    position: absolute;
    left: 18px;
    top: 17px;
    cursor: pointer;
    max-width: 19px;
    width: 100%;
    padding: 2px;
    @include media-breakpoint-down(lg) {
      display: inline-block;
    }
  }
  #open-menu {
    display: none;
    position: absolute;
    padding: 2px;
    top: 24px;
    right: 24px;
    cursor: pointer;
    @include media-breakpoint-down(lg) {
      display: inline-block;
    }
  }
}
