.business-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.b-tools {
  display: flex;
  flex-direction: row;
  max-width: 525px;
  margin-left: 450px;
  margin-top: 35px;
  @include media-breakpoint-down(lg) {
    margin-left: 40%;
  }
  @include media-breakpoint-down(md) {
    margin-left: auto;
    margin-top: 35px;
    margin-right: auto;
  }
  @include media-breakpoint-down(xs) {
    flex-direction: column;
    max-width: 205px;
  }
  &__links {
    margin-left: 125px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    @include media-breakpoint-down(xs) {
      margin-left: 0;
    }
    a {
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.49px;
      margin-bottom: 30px;
      display: block;
      @include sofia-medium;
      &:last-of-type {
        margin-bottom: 0;
        @include media-breakpoint-down(xs) {
          margin-bottom: 25px;
        }
      }
    }
  }
}
