.store-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.store-content {
  display: flex;
  flex-direction: row;
  padding: 0 15px;
  @include media-breakpoint-down(lg) {
    margin-top: 25px;
  }
  @include media-breakpoint-down(md) {
    flex-direction: column;
    margin-top: 0;
  }
  &__left {
    margin-right: auto;
    padding-right: 10px;
    padding-top: 110px;
    @include media-breakpoint-down(md) {
      max-width: 583px;
      margin: 0 auto;
      width: 100%;
      margin-bottom: 35px;
    }
    a {
      display: block;
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.49px;
      margin-bottom: 23px;
      @include sofia-medium;
      &:nth-of-type(2) {
        margin-bottom: 78px;
        @include media-breakpoint-down(md) {
          margin-bottom: 23px;
        }
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  &__center {
    margin-top: 44px;
    max-width: 583px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include media-breakpoint-down(md) {
      max-width: 583px;
      margin: 0 auto;
    }
    h3 {
      color: #444;
      font-size: 19px;
      margin-bottom: 15px;
      @include raleway-extrabold;
    }
    p {
      color: #8892a0;
      font-size: 11px;
      line-height: 16.07px;
      @include raleway-extrabold;
    }
    a {
      display: block;
      color: $color-pink;
      font-size: 31px;
      letter-spacing: 0.8px;
      margin-top: auto;
      align-items: flex-end;
      @include sofia-black;
      @include media-breakpoint-down(md) {
        margin-top: 23px;
      }
    }
  }
  &__right {
    max-width: 163px;
    padding-bottom: 35px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    @include media-breakpoint-down(md) {
      max-width: 583px;
      margin: 35px auto 0 auto;
    }
  }
}
