.top-hero {
  background-color: white;
  background-size: cover;
  background-position: center -100px;
  img {
    max-width: 605px;
    width: 100%
  }
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__welcome {
    margin-top: 123px;
    margin-bottom: 43px;
    @include flex-center;
    @include media-breakpoint-down(md) {
      margin-top: 100px;
    }
    @include media-breakpoint-down(sm) {
      margin-top: 75px;
    }
    @include media-breakpoint-down(xs) {
      margin-top: 50px;
    }
    strong {
      color: $color-dark-blue;
      font-size: 31px;
      letter-spacing: 1.5px;
      word-spacing: 5px;
      font-weight: 900;
      @include sofia-black;
    }
  }
  &__form {
    max-width: 540px;
    p {
      text-align: center;
      color: $color-dark-blue;
      margin-top: 18px;
      margin-bottom: 30px;
      font-size: 16px;
      letter-spacing: 1px;
      @include sofia-black;
    }
    form {
      @include flex-center;
      input {
        width: 100%;
        height: 40px;
        min-width: 435px;
        padding: 14px;
        background-color: white;
        border: 1px solid #ced4da;
        @include media-breakpoint-down(xs) {
          min-width: 205px;
        }
        &::placeholder {
          color: #868e96;
          font-size: 16px;
          @include lato-regular;
        }
      }
      button {
        background-color: $color-dark-blue;
        color: white;
        font-size: 16px;
        letter-spacing: 0.4px;
        width: 105px;
        height: 40px;
        border: 1px solid $color-dark-blue;
        @include lato-regular;
      }
    }
  }
  &__bottom {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    @include media-breakpoint-down(sm) {
      display: none;
    }
  }
}
