.side {
  width: 205px;
  cursor: pointer;
  &__top {
    width: 100%;
    height: 205px;
    background-color: #fbfbfb;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h3 {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      min-height: 45px;
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.57px;
      max-width: 170px;
      text-align: center;
      margin-bottom: 22px;
      font-style: italic;
      @include raleway-regular;
    }
    h4 {
      color: #383838;
      font-size: 13px;
      letter-spacing: 0.59px;
      max-width: 170px;
      text-align: center;
      margin-bottom: 30px;
      white-space: nowrap;
      @include raleway-regular;
    }
    &__triangle {
      max-width: 27px;
      img {
        width: 100%;
      }
    }
  }
  &__bottom {
    padding: 0 24px;
    width: 100%;
    height: 85px;
    background-color: #daebf6;
    @include flex-center;
    p {
      color: #383838;
      font-size: 13px;
      letter-spacing: 0.5px;
      @include raleway-regular;
    }
  }
  &__link {
    margin-top: 20px;
    a {
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.49px;
      @include sofia-medium;
    }
  }
  &__triangle {
    width: 100%;
    img {
      max-width: 35px !important;
      margin: 0 auto;
      display: block;
    }
  }
}
