.session-background {
  background-color: white;
  background-size: cover;
  background-position: center -100px;
  img {
    display: block;
    width: 100%;
    max-width: 510px;
    margin: 0 auto;
    padding: 63px 10px 33px 10px;
  }
}
.session-header {
  @include flex-center;
  @include media-breakpoint-down(md) {
    padding: 0 30px;
  }
  h3 {
    color: $color-dark-blue;
    text-align: center;
    font-size: 21px;
    line-height: 27px;
    max-width: 730px;
    @include raleway-regular;
    strong {
      @include raleway-bold;
    }
    @include media-breakpoint-down(md) {
      font-size: 19px;
      line-height: 20px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 16px;
      line-height: 16px;
    }
    @include media-breakpoint-down(xs) {
      font-size: 14px;
      line-height: 14px;
    }
  }
}
.white-banner {
  padding: 37px 15px 52px 15px;
  margin-top: 23px;
  margin-bottom: 40px;
  background-color: white;
  width: 100%;
  @include flex-center;
  @include media-breakpoint-down(xs) {
    padding: 30px 15px 42px 15px;
  }
  a {
    color: $color-pink;
    font-size: 80px;
    letter-spacing: 0.8px;
    @include sofia-black;
    &:last-of-type {
      @include media-breakpoint-down(sm) {
        margin-top: 30px;
      }
    }
    @include media-breakpoint-down(md) {
      font-size: 58px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 38px;
    }
  }
  &__inner {
    width: 100%;
    max-width: 1185px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @include media-breakpoint-down(md) {
      max-width: 750px;
    }
    @include media-breakpoint-down(sm) {
      flex-direction: column;
      text-align: center;
    }
  }
}
