.newproducts-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.product-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 36px;
  @include media-breakpoint-down(md) {
    flex-direction: column;
  }
  a {
    color: $color-dark-blue;
    font-size: 19px;
    letter-spacing: 0.49px;
    margin-bottom: 18px;
    display: block;
    width: 100%;
    @include sofia-medium;
  }
  h3 {
    color: #444;
    font-size: 19px;
    margin-bottom: 15px;
    width: 100%;
    @include raleway-extrabold;
  }
  &__left {
    .product-list__section {
      max-width: 475px;
      padding-left: 15px;
      @include media-breakpoint-down(md) {
        padding-left: 0;
      }
    }
    @include media-breakpoint-down(md) {
      margin-top: 25px;
    }
  }
  &__section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 550px;
    width: 100%;
    margin-bottom: 67px;
    flex-wrap: wrap;
  }
  &__column {
    display: block;
    max-width: 275px;
    width: 100%;
  }
}
.newproducts {
  .three-column {
    padding: 132px 15px 0 15px;
  }
}
