.faq-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.questions {
  margin-left: auto;
  margin-top: 35px;
  max-width: 750px;
  width: 100%;
  @include media-breakpoint-down(md) {
    margin-left: 0;
    margin-top: 55px;
  }
  h3 {
    color: #444;
    font-size: 19px;
    margin-bottom: 21px;
    @include raleway-extrabold;
  }
  p {
    color: #8892a0;
    font-size: 11px;
    line-height: 16.07px;
    @include raleway-extrabold;
  }
  &__section {
    margin-bottom: 35px;
    &:last-of-type {
      margin-bottom: 18px;
    }
  }
}
