.help-background {
  background-color: white;
  background-size: cover;
  background-position: center -90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img {
    width: 100%;
    max-width: 350px;
    margin-top: 45px;
    margin-bottom: 70px;
  }
  h2 {
    color: $color-dark-blue;
    font-size: 36px;
    margin-bottom: 20px;
    text-align: center;
    @include lato-bold;
  }
  h3 {
    color: $color-dark-blue;
    font-size: 26px;
    margin-bottom: 20px;
    text-align: center;
    @include raleway-semibbold;
  }
  .btn-blue {
    margin-top: -15px;
    @media (max-width: 767px) {
      margin-top: 0;
    }
  }
}
.help-box {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0 15px;
  &__inner {
    max-width: 916px;
    width: 100%;
  }
  &__columns {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    @media (max-width: 575px) {
      flex-direction: column;
    }
  }
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    input {
      width: 100%;
      height: 32px;
      padding: 11px;
      background-color: white;
      border-radius: 4px;
      border: 1px solid #ced4da;
      margin-right: 25px;
      @media (max-width: 767px) {
        margin-right: 13px;
      }
      @media (max-width: 575px) {
        margin-bottom: 10px;
        margin-right: 0;
      }
      &:last-of-type {
        margin-right: 0;
        @media (max-width: 575px) {
          margin-bottom: 0;
        }
      }
      &::placeholder {
        color: #868e96;
        font-size: 13px;
        @include lato-regular;
      }
    }
    textarea {
      width: 100%;
      height: 71px;
      padding-left: 11px;
      padding-top: 8px;
      background-color: white;
      border-radius: 4px;
      border: 1px solid #ced4da;
      resize: none;
      &::placeholder {
        color: #868e96;
        font-size: 13px;
        @include lato-regular;
      }
      &:first-of-type {
        height: 30px;
        margin-bottom: 19px;
        margin-top: 19px;
      }
    }
    button {
      background-color: $color-dark-blue;
      color: white;
      font-size: 16px;
      letter-spacing: 0.4px;
      width: 105px;
      height: 40px;
      border-radius: 2px;
      border: 1px solid $color-dark-blue;
      margin-top: 23px;
      @include lato-regular;
      @media (max-width: 767px) {
        margin-bottom: 25px;
      }
    }
  }
}
