.agenda-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.table-text {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  max-width: 750px;
  margin-left: auto;
  margin-top: 35px;
  @include media-breakpoint-down(md) {
    margin-top: 35px;
  }
  @include media-breakpoint-down(sm) {
    max-width: 400px;
    margin-right: auto;
  }
  @include media-breakpoint-down(xs) {
    max-width: 205px;
    margin-left: 0;
  }
  h3 {
    color: #444;
    font-size: 18px;
    margin-bottom: 14px;
    @include raleway-extrabold;
  }
  p {
    color: #8892a0;
    font-size: 18px;
    line-height: 24.04px;
    @include raleway-regular;
  }
  &__section {
    margin-right: 45px;
    margin-bottom: 75px;
    @include media-breakpoint-down(sm) {
      margin-right: 0;
    }
    @include media-breakpoint-down(xs) {
      margin-bottom: 45px;
    }
  }
}
.table {
  display: flex;
  flex-wrap: wrap;
  .three-column__right {
    h2 {
      margin-bottom: 70px;
    }
  }
  &__left {
    margin-right: auto;
    padding-right: 10px;
    padding-top: 50px;
    @include media-breakpoint-down(md) {
      max-width: 583px;
      margin: 0 auto;
      width: 100%;
      margin-bottom: 35px;
    }
    a {
      display: block;
      color: $color-dark-blue;
      font-size: 19px;
      letter-spacing: 0.49px;
      margin-bottom: 23px;
      @include sofia-medium;
      &:nth-of-type(2) {
        margin-bottom: 78px;
        @include media-breakpoint-down(md) {
          margin-bottom: 23px;
        }
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}
