.bonus-background {
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: -180px;
  background-position-x: center;
}
.bonus-add {
  margin-top: 32px;
  @include media-breakpoint-down(md) {
    margin-top: 60px;
  }
  &__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 785px;
    flex-wrap: wrap;
    margin-left: auto;
    @include media-breakpoint-down(md) {
      margin-right: auto;
    }
    @include media-breakpoint-down(sm) {
      max-width: 435px;
    }
    @include media-breakpoint-down(xs) {
      max-width: 205px;
    }
  }
  .side {
    margin-bottom: 43px;
    cursor: pointer;
    &:last-of-type {
      margin-bottom: 100px;
    }
  }
}
.bonus, .business-tools, .expert-panels {
  .side__triangle {
    &:before {
      content: none !important;
    }
  }
}
